.header {
    background: #a11313;
    background: linear-gradient(to top left, #ffeed6 0%, #93c4ef 36%, #1f89c9 100%);
    grid-area: h;
  }
  
  .header .icon {
      padding-left: 48px;
      padding-top: 5px;
      width: 50px;
  }

  .loginBlock {
      float: right;
      margin-right: 15px;
  }

.loginBlock .loginIcon {
    width: 50px;
    padding-top: 5px;
}

.loginBlock a {
    color: black;
    text-decoration: none;
    font-size: 18pt;
    vertical-align: top;
}