.nav {
  background-color: #9dc7e0;
  grid-area: n;
  padding-top: 20px;
  text-align: center;
  font-size: 18pt;
}

.nav .item a {
  color: white;
  text-decoration: none;
}

.nav .item .active {
  color: #0b085a;
}

.nav .item.friendsEl {
  padding-top: 30px;
  width: 120px;
  margin: 0 auto;

}

.nav .item.friendsEl img {
  margin: 2px;
  width: 30px;
  background-color: white;
  border: white solid 1px;
  border-radius: 30px;
}